body{
    background-color:rgb(243, 237, 229);
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.image{
    width: 350px;
    margin-top: 50px;
}

@media(max-width:750px){
    .container{
        margin:0 10%;
    }
    image{
        width: 100%;
    }
}